body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  box-sizing: border-box;

  --text-color-1: #333;
  --text-color-2: rgb(240, 240, 240);
}

*, *::after, *::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.App {
  max-width: 50em;
  margin: 0 auto;
}

.header {
    font-size: 1.1em;
    margin: 1em auto 2em;
    text-align: center;
    color: var(--text-color-1);
}


.todo-item {
    font-size: 1.2em;
    padding: 1em 2em;
    margin-bottom: 1em; 
    cursor: pointer;
    background-color: rgb(91, 91, 121);
    border-radius: 1em;
    color: var(--text-color-2);
    display: flex;
    justify-content: space-between;
}


.todo-item--completed {
    text-decoration: line-through;
    background-color: rgb(71, 156, 71);
}
.todo-item:hover {
    background-image: -webkit-linear-gradient( rgba(255, 255, 255, 0.233), rgba(0, 0, 0, 0.171));
    background-image: linear-gradient( rgba(255, 255, 255, 0.233), rgba(0, 0, 0, 0.171));
}
.todo-item:active {
    background-image: -webkit-linear-gradient( rgba(0, 0, 0, 0.171),rgba(255, 255, 255, 0.233));
    background-image: linear-gradient( rgba(0, 0, 0, 0.171),rgba(255, 255, 255, 0.233));
}
